import { UserActionType } from "./user.type";

const initialState = {
    currentUser: null
}

const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case UserActionType.SET_CURRENT_USER:
            return {
                ...state,
                currentUser: action.payload,
            }

        default: return state;
    }
}

export default userReducer;